<template>
<div>
    <div class="newinfo-page">
        <div class="newinfo-title">
            <span>新建病人档案</span>
        </div>
        <div class="newinfo-input-info">
            <input placeholder="姓名" v-model="name" class="newinfo-input-tall-weight">
        </div>
        <div class="newinfo-input-info">
            <input placeholder="身份证号" v-model="id" maxlength="18" minlength="18" class="newinfo-input-tall-weight">
        </div>
        <div class="newinfo-submit-btn" @click="submitNewinfor">
            <span>下一步</span>
        </div>
    </div>
</div>
</template>

<script>
import {
    newPatient
} from '../../api'
export default {
    data() {
        return {
            name: '',
            id: ''
        }
    },
    methods: {
        getInfoById() {
            const {
                id
            } = this
            let sex = '男'
            if (id[16] == 1) {

            } else {
                sex = '女'
            }
            let myDate = new Date();
            let month = myDate.getMonth() + 1;
            let day = myDate.getDate();
            let age = myDate.getFullYear() - id.substring(6, 10) - 1;
            if (id.substring(10, 12) < month || id.substring(10, 12) == month && id.substring(12, 14) <= day) {
                age++;
            }
            return {
                age: age,
                sex: sex
            }
        },
        submitNewinfor() {
            //身份证校验函数
            function checkIDCard(idcode) {
                // 加权因子
                var weight_factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
                // 校验码
                var check_code = ['1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2'];

                var code = idcode + "";
                var last = idcode[17]; //最后一位

                var seventeen = code.substring(0, 17);

                // ISO 7064:1983.MOD 11-2
                // 判断最后一位校验码是否正确
                var arr = seventeen.split("");
                var len = arr.length;
                var num = 0;
                for (var i = 0; i < len; i++) {
                    num = num + arr[i] * weight_factor[i];
                }

                // 获取余数
                var resisue = num % 11;
                var last_no = check_code[resisue];

                // 格式的正则
                // 正则思路
                /*
                第一位不可能是0
                第二位到第六位可以是0-9
                第七位到第十位是年份，所以七八位为19或者20
                十一位和十二位是月份，这两位是01-12之间的数值
                十三位和十四位是日期，是从01-31之间的数值
                十五，十六，十七都是数字0-9
                十八位可能是数字0-9，也可能是X
                */
                var idcard_patter = /^[1-9][0-9]{5}([1][9][0-9]{2}|[2][0][0|1][0-9])([0][1-9]|[1][0|1|2])([0][1-9]|[1|2][0-9]|[3][0|1])[0-9]{3}([0-9]|[X])$/;

                // 判断格式是否正确
                var format = idcard_patter.test(idcode);

                // 返回验证结果，校验码和格式同时正确才算是合法的身份证号码
                return last === last_no && format ? true : false;
            }
            let flag = checkIDCard(this.id)
            if (!this.name) {
                this.$message({
                    message: '请输入姓名',
                    center: true,
                    type: 'error'
                });
                return;
            } else if (!flag) { //身份证校验不过给出提示
                this.$message({
                    message: '身份证不合法',
                    type: 'error'
                })
                return;
            } else {
                let docId = sessionStorage.getItem('doc_id')
                newPatient({
                    params: {
                        "doctor_id": docId,
                        "identify_card": this.id,
                        "name": this.name
                    },
                    success: res => {
                        sessionStorage.setItem("idAddingCase", this.id);
                        this.$message({
                            message: '信息已上传',
                            type: 'success'
                        })

                        this.$router.push({
                            name: 'addcase',
                            query: {
                                identify_card: this.id,
                                name: this.name,
                                ...this.getInfoById(),
                                to: 'manager'
                            }
                        })
                        let data = {
                            name: '新增影像病历',
                            path: '/index/addcase'
                        }
                        this.$store.commit('addBreadcrumbData', data)

                    },
                    fail: res => {
                        this.$message({
                            message: res.data,
                            type: 'warning'
                        })
                    },
                    error: err => {
                        this.$message({
                            message: res.data,
                            type: 'warning'
                        })
                    }
                })

                // this.axios.post(`/patientInfo/newPatientFiles/submission`, {
                //     "doctor_id": docId,
                //     "identify_card": this.id,
                //     "name": this.name
                // }).then(res => {
                //     if (res.status === 200 && res.data.code === 0) {
                //         sessionStorage.setItem("idAddingCase", this.id);
                //         this.$message({
                //             message: '信息已上传',
                //             type: 'success'
                //         })
                //     } else {
                //         this.$message({
                //             message: res.data.data,
                //             type: 'warning'
                //         })
                //     }
                // }).catch(err => {
                //     this.$message({
                //         message: res.data.data,
                //         type: 'warning'
                //     })
                // })
            }
        }

    }
}
</script>

