<template>
<div class="manager-page">
    <div class="manager-page-header">
        <div class="search-input-view">
            <input placeholder="请输入查找的信息" v-model="keywords">
            <i class="iconfont iconchazhao" @click="submitSearchInput" v-if="hasResult"></i>
            <i class="iconfont icontubiao-" @click="cancelSearch" v-if="!hasResult"></i>
        </div>
        <div class="manager-page-header-btn">
            <div class="manager-page-header-editBtn" @click="newInfo">
                <i class="iconfont iconxinjian"></i>
                <span>新建患者档案</span>
            </div>
            <div class="manager-page-header-editBtn" @click="multiplyDelete">
                <i class="iconfont iconshanchu"></i>
                <span>批量删除</span>
            </div>
        </div>
    </div>
    <div class="manager-page-body">
        <el-table :data="tableData" @selection-change="handleSelectionChange" stripe header-cell-class-name="cell-text-center" cell-class-name="cell-text-center">
            <el-table-column class="el-table-selection" type="selection"></el-table-column>
            <el-table-column label="序号" type="index" :index="indexMethod" width=65></el-table-column>
            <el-table-column prop="name" label="姓名"></el-table-column>
            <el-table-column prop="identify_card" label="身份证号"></el-table-column>
            <el-table-column prop="sex" label="性别"> </el-table-column>
            <el-table-column prop="age" label="年龄"> </el-table-column>
            <el-table-column prop="edit" label="编辑">
                <template slot-scope="scope">
                    <el-button @click.native.prevent="browseInfo(scope.row,tableData)" class="el-btn-item" type="text">查看</el-button>
                    <!-- <span v-if="IsLoadingSuccess() === true">查看</span>
                   进度条
                    <div v-if="IsLoadingSuccess() === false">
                        <el-progress type="circle" :percentage="25"></el-progress>
                    </div> -->

                    <el-button @click.native.prevent="addCase(scope.row,tableData)" class="el-btn-item" type="text">新增</el-button>
                    <el-button @click.native.prevent="deleteInfo(scope.row,tableData)" class="el-btn-item" type="text">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
    <div class="manager-page-footer">
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :total="sum_patients"></el-pagination>
    </div>
</div>
</template>

<script>
import {
    getDossierList
} from '../../api'
import {
    getSearchList
} from '../../api'
import {
    delPatient
} from '../../api'

export default {

    data() {
        return {
            keywords: '',
            tableData: [],
            pageSize: 10,
            pageNum: 1,
            deleteAllData: [],
            sum_patients: 0,
            hasResult: true,
            timer: null
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.getTableData()
        })
    },
    methods: {
        indexMethod(index){
            return(index+1)+(this.pageNum-1)*this.pageSize;
        },
        IsLoadingSuccess() {
            let sessionIdLoad = sessionStorage.getItem('idAddingCase')
            this.tableData.map(item => {
                if (item.id === sessionIdLoad) {
                    return false;
                } else {
                    return true;
                }
            })
        },
        submitSearchInput() {
            getSearchList({
                params: {
                    docId: sessionStorage.getItem('doc_id'),
                    keywords: this.keywords,
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                },
                success: res => {
                    this.tableData = res.data.patients
                    this.hasResult = false
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    this.$message({
                        message: "服务器异常",
                        type: 'warning'
                    })
                }
            })

            // this.axios.get(`/patientInfo/patientFiles/${docId}?query=${this.keywords}&pagenum=${this.pageNum}&pagesize=${this.pageSize}`).then(res => {
            //     if (res.status === 200 && res.data.code === 0) {
            //         this.tableData = res.data.data.patients
            //         this.hasResult = false
            //     } else {
            //         this.$message({
            //             message: res.data.msg,
            //             type: 'warning'
            //         })
            //     }
            // }).catch(err => {
            //     this.$message({
            //         message: "服务器异常",
            //         type: 'warning'
            //     })
            // })
        },
        cancelSearch() {
            this.keywords = ''
            this.hasResult = true
            this.getTableData()
        },
        handleCurrentChange(page) {
            this.pageNum = page
            this.getTableData()
        },
        getTableData() {
            //请求服务器数据
            //eg：
            //获取档案列表
            getDossierList({
                params: {
                    docId: sessionStorage.getItem('doc_id'),
                    pageNum: this.pageNum,
                    pageSize: this.pageSize
                },
                success: (res) => {
                    this.tableData = res.data.patients
                    this.sum_patients = res.data.sum_patients
                },
                fail: (err) => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: (err) => {
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
        },
        //     this.axios({
        //         method: 'get',
        //         url: `/patientInfo/patientFiles/${docId}?pagenum=${this.pageNum}&pagesize=${this.pageSize}`
        //     }).then(res => {
        //         if (res.status === 200 && res.data.code === 0) {
        //             this.tableData = res.data.data.patients
        //             this.sum_patients = res.data.data.sum_patients
        //         } else {
        //             this.$message({
        //                 message: res.data.msg,
        //                 type: 'warning'
        //             })
        //         }

        //     }).catch(err => {
        //         this.$message({
        //             message: '服务器异常',
        //             type: 'warning'
        //         })
        //     })
        // },
        // getSearchInfo(){},
        newInfo() {
            this.$router.replace({
                name: 'newinfo'
            })
            let data = {
                name: '新建患者档案',
                path: '/index/newinfo'
            }
            this.$store.commit('addBreadcrumbData', data)
        },
        browseInfo(item) {
            this.$router.push({
                name: 'detail',
                query: {
                    ...item
                }
            })
            let data = {
                name: '患者影像数据',
                path: '/index/detail'
            }
            this.$store.commit('addBreadcrumbData', data)
        },
        addCase(item) {
            sessionStorage.setItem('id', item.identify_card)
            this.$router.replace({
                name: 'addcase',
                query: {
                    ...item,
                    to: 'manager'
                }
            })
            let data = {
                name: '新增影像病历',
                path: '/index/addcase'
            }
            this.$store.commit('addBreadcrumbData', data)
        },
        deleteCase(param) {
            delPatient({
                params: {
                    "identify_card": param
                },
                success: res => {
                    this.getTableData()
                    this.$message({
                        message: "删除成功",
                        type: 'success'
                    })
                },
                fail: res => {
                    this.getTableData()
                    this.$message({
                        message: "删除失败",
                        type: 'warning'
                    })
                },
                error: err => {
                    this.$message({
                        message: "删除失败",
                        type: 'warning'
                    })
                }
            })

            // this.axios.post('/patientInfo/del', {
            //     "identify_card": param
            // }).then(res => {
            //     if (res.data.code === 0) {
            //         this.getTableData()
            //         this.$message({
            //             message: "删除成功",
            //             type: 'success'
            //         })
            //     } else {
            //         this.getTableData()
            //         this.$message({
            //             message: "删除失败",
            //             type: 'warning'
            //         })
            //     }
            // }).catch(err => {
            //     this.$message({
            //         message: "删除失败",
            //         type: 'warning'
            //     })
            // })
        },
        multiplyDelete() {
            if (this.deleteAllData.length === 0) {
                this.$message({
                    message: '请先选择要删除的数据',
                    type: 'warning'
                })
            } else {
                this.deleteCase(this.deleteAllData)
            }

        },
        deleteInfo(item, data) {
            this.deleteCase([item.identify_card])
        },
        handleSelectionChange(selection) {
            let result = []
            // 循环操作
            selection.map(item => {
                result.push(item.identify_card)
            })
            this.deleteAllData = result
        }
    }
}
</script>

