import Vue from 'vue'
export default {
    state: {
        breadcrumbData: (function(){
            let result = JSON.parse(sessionStorage.getItem('breadcrumbData'))
            if (!result) {
                result = [{
                    name: '档案管理',
                    path: '/index/manager'
                }]
            }
            return  result
        })()
    },
    mutations: {
        addBreadcrumbData(state, data) {
            let ret = state.breadcrumbData.find(item => {
                return data.path === item.path
            })
            if (ret) {
                return
            } else {
                state.breadcrumbData.push(data)
            }
            sessionStorage.setItem('breadcrumbData', JSON.stringify(state.breadcrumbData))
        },
        popBreadcrumbData(state, index) {
            let arr = state.breadcrumbData
            let result = [];
            for (let i = 0; i <= index; i++) {
                result.push(arr[i])
            }
            state.breadcrumbData = result
            sessionStorage.setItem('breadcrumbData', JSON.stringify(state.breadcrumbData))
        }
    }
}
