<template>
<div class="resetpwd-page">
    <div class="resetpwd-view">
        <div class="resetpwd-view-header">
            <i class="iconfont iconfanhui" @click="backLogin"></i>
        </div>
        <div class="resetpwd-view-content">
            <div class="resetpwd-form">
                <span class="resetpwd-form-title">重设密码</span>
                <div class="resetpwd-input-view">
                    <span class="area-code">{{areacode}}</span>
                    <span class="spliter"></span>
                    <div class="input-view">
                        <input type="text" v-model="restetPhoneNum" placeholder="手机号码">
                    </div>
                </div>
                <div class="identify-code">
                    <div class="input-identify-code">
                        <input type="text" v-model="identifyCode" placeholder="短信验证码">
                    </div>
                    <!-- <div class="identify-code-btn" @click="getIdentify">获取验证码</div> -->
                    <span class="identify-code-btn" v-show="show" @click="getCode">获取验证码</span>
                    <span v-show="!show" class="count">{{count}}s后重试</span>
                    <!-- <van-button class="identify-code-btn" @click="getyzcode" :disabled="attcode" v-if="showbtn">获取验证码</van-button> -->
                </div>

                <div class="resetpwd-input-view">
                    <i class="iconfont iconmima"></i>
                    <span class="spliter"></span>
                    <div class="input-view">
                        <input type="password" v-model="newPwd" placeholder="新密码">
                    </div>
                </div>
                <div class="resetpwd-input-view">
                    <i class="iconfont iconmima"></i>
                    <span class="spliter"></span>
                    <div class="input-view">
                        <input type="password" v-model="checkNewPwd" placeholder="确认密码">
                    </div>
                </div>
                <div class="submit-resetpwd-btn" @click="submitResetPwd">
                    <span>重设密码</span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
let sha1 = require('js-sha1')
import {
    securityCode
} from '../../api'
import {
    resetPwd
} from '../../api'
export default {
    data() {
        return {
            areacode: '+86',
            restetPhoneNum: '',
            identifyCode: '',
            newPwd: '',
            checkNewPwd: '',
            // confirm: true, //提交验证按钮判断
            // attcode: true, //点击获取验证码按钮判断
            // showbtn: true, // 展示获取验证码或倒计时按钮判断
            // yzcode: '',
            // code_ts: '获取验证码', //倒计时提示文字
            // sec: 60, // 倒计时秒数
            show: true,
            count: '',
            timer: null,
        }
    },

    methods: {
        backLogin() {
            this.$router.replace({
                name: 'login'
            })
        },
        getIdentify() {
            if (this.restetPhoneNum) {
                securityCode({
                    params: {
                        "phone_number": this.restetPhoneNum,
                        "pre_phone_number": this.areacode,
                    },
                    success: res => {
                        this.$message({
                            message: '正在获取验证码，请稍后',
                            type: 'success'
                        })
                    },
                    fail: res => {
                        this.$message({
                            message: '服务器异常',
                            type: 'warning'
                        })
                    },
                    error: err => {
                        this.$message({
                            message: '服务器异常',
                            type: 'warning'
                        })
                    }
                })

                // this.axios.post(`/identify`, {
                //     "phone_number": this.restetPhoneNum,
                //     "pre_phone_number": this.areacode,
                // }).then(res => {
                //     if (res.status === 200 && res.data.code === 0) {
                //         this.$message({
                //             message: '正在获取验证码，请稍后',
                //             type: 'success'
                //         })
                //         this.$router
                //     } else {
                //         this.$message({
                //             message: '服务器异常',
                //             type: 'warning'
                //         })
                //     }
                // }).catch(err => {
                //     this.$message({
                //         message: '服务器异常',
                //         type: 'warning'
                //     })
                // })
            } else {
                this.$message({
                    message: '请输入手机号码',
                    type: 'warning'
                })
            }
        },
        getCode() {
            const TIME_COUNT = 60;
            this.getIdentify()
            if (!this.timer) {
                this.count = TIME_COUNT;
                this.show = false;
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                        this.count--;
                    } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000)
            }
        },
        // getyzcode() {
        //     var timer = setInterval(() => {
        //         this.sec = this.sec - 1
        //         this.code_ts = this.sec + 'S后重试'
        //         this.showbtn = false
        //         if (this.sec === 0) {
        //             clearInterval(timer)
        //             this.sec = 60
        //             this.code_ts = this.sec + 'S后重试'
        //             this.showbtn = true
        //         }
        //     }, 1000)
        // },

        submitResetPwd() {
            if (!this.identifyCode) {
                this.$message({
                    message: '请输入短信验证码',
                    type: 'warning'
                })
                return;
            } else if (!this.newPwd) {
                this.$message({
                    message: '请输入新密码',
                    type: 'warning'
                })
                return;
            } else if (!this.checkNewPwd) {
                this.$message({
                    message: '请确认密码',
                    type: 'warning'
                })
                return;
            }
            let newPwdS = sha1(this.newPwd)
            resetPwd({
                params: {
                    // "password": this.newPwd,
                    "password": newPwdS,
                    "identify": this.identifyCode,
                    "phone_number": this.restetPhoneNum,
                    "pre_phone_number": this.areacode
                },
                success: res => {
                    this.$message({
                        message: '密码已更新，请重新登录',
                        type: 'success'
                    })
                    this.$router.replace({
                        name: 'login'
                    })
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                erroe: err => {
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
            return;
            // this.axios.post(`/reset_password`, {
            //     // "password": this.newPwd,
            //     "password": newPwdS,
            //     "identify": this.identifyCode,
            //     "phone_number": this.restetPhoneNum,
            //     "pre_phone_number": this.areacode
            // }).then(res => {
            //     if (res.status === 200 && res.data.code === 0) {
            //         this.$router.replace({
            //             name: 'login'
            //         })
            //     } else {
            //         this.$message({
            //             message: res.data.msg,
            //             type: 'warning'
            //         })
            //     }
            // }).catch(err => {
            //     this.$message({
            //         message: '服务器异常',
            //         type: 'warning'
            //     })
            // })
        }
    }
}
</script>

