<template>
<div class="home-page">
    <div class="page-header">
        <div class="page-header-title">
            <span>肝脏影像分析平台</span>
        </div>
        <div class="page-header-right">
            <div class="notice-help-upgrade">
                <i class="iconfont icontongzhi"></i>
                <span>通知</span>
            </div>
            <div class="notice-help-upgrade">帮助</div>
            <div class="notice-help-upgrade">升级</div>
            <div class="quit">
                <div class="circle"> </div>
                <div class="quit-words" @click="logout">退出</div>
            </div>

        </div>
    </div>
    <div class="page-container">
        <div class="page-aside">
            <i class="iconfont icondanganhe"></i>
            <span>患者档案管理</span>
            <!-- <ul><li v-for="(item,index) in asideNav" v-bind:key="index.toString()" v-on:click="linkTo(item.path)" v-bind:class="getAsideClass(item.path)">{{item.label}}</li>  </ul>-->

        </div>
        <div class="page-content">
            <div class="page-content-header">
                <!-- 面包屑导航 -->
                <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item v-for="(item,index) in $store.state.breadcrumbData" :key="index.toString()">
                        <span @click="linkToBreadcrumb(item.path,index)" :class="index === $store.state.breadcrumbData.length - 1 ? 'active-breadcrumb':'breadcrumb'">
                            {{item.name}}
                        </span>
                    </el-breadcrumb-item>
                </el-breadcrumb>
                <div class="page-content-header-right">
                    <div class="docoterName-dep-date"> 医生姓名:<span>{{docName}}</span></div>
                    <div class="docoterName-dep-date">科室：<span>{{docDep}}</span></div>
                    <div class="docoterName-dep-date">日期：<span>{{thisDay}}</span></div>
                </div>
            </div>
            <router-view class="page-content-body"></router-view>
        </div>
    </div>
</div>
</template>

<script>
import moment from 'moment'
export default {
    name: 'App',
    data() {
        return {
            asideNav: [{
                path: '/index/manager',
                label: '档案管理页面'
            }],
            activeNav: '/index/manager', //默认界面
            docName: '',
            docDep: '',
            thisDay: ''
        }

    },
    methods: {
        linkTo: function (path) {
            this.activeNav = path //换样式
            this.$router.push(path) //点击跳转
        },

        getAsideClass: function (path) {
            let x = this.activeNav === path ? 'aside-item-active' : ''
            return ['aside-item', x]
            //三元表达式 return['aside-item',this.activeNav === path ? 'aside-item-active' : '']
        },
        linkToBreadcrumb(path, index) {
            this.$store.commit('popBreadcrumbData', index)
            let query = this.$route.query
            delete query.case_id
            this.$router.replace({
                path: path,
                query: {
                    ...query
                } //面包屑导航跳转时需要带上query参数
            })
        },
        logout() {
            sessionStorage.clear()
            localStorage.clear()
            this.$store.commit('popBreadcrumbData', 0)
            this.$router.replace({
                name: 'login'
            })
        }
    },
    mounted() {
        //添加时间插件获取当前时间
        this.thisDay = moment().format('YYYY-MM-DD')
        this.$nextTick(() => {
            // this.activeNav = this.$route.path
            this.docName = sessionStorage.getItem('doc_name');
            this.docDep = sessionStorage.getItem('doc_dep');

        })
    }
}
</script>

