<template>
<div>
    <div class="addcase-page">
        <div class="addcase-title">
            <span>新增影像病历</span>
        </div>
        <div class="addcase-info">
            <div class="addcase-info-information">
                <div class="addcase-info-tall-weight">
                    <div class="addcase-input-info">
                        <input placeholder="身高" v-model="tall" class="addcase-input-tall-weight">
                        <span class="tall-weight-icon">cm</span>
                    </div>
                    <div class="addcase-input-info">
                        <input placeholder="体重" v-model="weight" class="addcase-input-tall-weight">
                        <span class="tall-weight-icon">kg</span>
                    </div>
                </div>
                <div class="addcase-info-remark">
                    <!-- <textarea placeholder="备注：" v-model="remark"></textarea> -->
                    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入备注信息" v-model="remark">
                    </el-input>
                    <!-- <input type="text" placeholder="备注：" v-model="remark"> -->
                </div>
            </div>
            <div v-if="activeCheck === 1" class="submit-addcase-btn" @click="submitAddcase">
                <span>确认信息</span>
            </div>
        </div>
        <!-- headers="multipart/form-data" -->
        <div class="upload-box">
            <vue-dropzone class="upload-demo" v-if="dropzoneOptions.url" ref="myVueDropzone" id="dropzone" :options="dropzoneOptions"></vue-dropzone>
            <span class="upload-tips" v-if="!dropzoneOptions.url">请先确认身高体重信息之后再上传文件</span>
            <!-- <el-upload class="upload-demo" webkitdirectory="" accept=".dcm" :before-upload="beforeUpload" drag :action="`/uploads/${caseId}`" multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            </el-upload> -->
        </div>
        <div v-if="activeFile === 1" class="submit-addcase-file-btn" @click="submitCaseFile">
            <span>上传完成</span>
        </div>
    </div>
</div>
</template>

<script>
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import {
    newCase
} from '../../api'
import {
    execute
} from '../../api'
export default {
    name: 'app',
    components: {
        vueDropzone: vue2Dropzone
    },
    data() {
        let vm = this
        return {
            tall: '',
            weight: '',
            remark: '',
            caseId: '',
            activeCheck: 1,
            activeFile: 0,
            dropzoneOptions: {
                url: '',
                thumbnailWidth: 50,
                thumbnailHeight: 50,
                headers: {
                    "My-Awesome-Header": "header value"
                },
                acceptedFiles: '.dcm,.rar,.zip,.7z,.CAB,.ARJ,.LZH,.TAR,.GZ,.ACE,.UUE,.BZ2,.JAR,.ISO,.MPQ',
                init() {
                    this.on("queuecomplete", function (file) {
                        vm.activeFile = 1
                        // this.initMessage()
                    })
                }
            },

        }
    },
    mounted() {
        
    },
    methods: {
        //文件上传之前的回调函数
        beforeUpload() {
            // 校验caseId是否存在
            // caseId不存在则提示并且中断上传操作
            // caseId存在则继续上传
            if (!this.caseId) {
                this.$message({
                    message: "请先确认基本信息",
                    type: 'warning'
                })
                //返回false会中断上传操作
                return false
            } else {
                //返回true会继续上传操作
                return true
            }

        },
        submitAddcase() {
            //提交添加病例
            //检验参数是否完整 不完整则提示并中断函数执行
            if (!this.tall || !this.weight) {
                this.$message({
                    message: "请先填写身高体重",
                    type: 'warning'
                })
                return
            }
            newCase({
                params: {
                    "height": this.tall,
                    "identify_card": this.$route.query.identify_card,
                    "information": this.remark,
                    "weight": this.weight
                },
                success: res => {
                    this.caseId = res.data
                    this.activeCheck = 0
                    this.$set(this.dropzoneOptions, 'url', `/uploads/${res.data}`)
                    this.$message({
                        message: "信息已上传",
                        type: 'success'
                    })
                    sessionStorage.getItem('submitInfo', true)
                },
                fail: res => {
                    //添加失败时 提示后台返回的失败原因  res.msg
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    this.$message({
                        message: "服务器异常",
                        type: 'warning'
                    })
                }
            })

            // this.axios.post(`/patientInfo/newPatientCases/submission`, {
            //     "height": this.tall,
            //     "identify_card": this.$route.query.identify_card,
            //     "weight": this.weight
            // }).then(res => {
            //     if (res.status === 200 && res.data.code === 0) {
            //         this.caseId = res.data.data
            //         this.$set(this.dropzoneOptions, 'url', `/uploads/${res.data.data}`)
            //         this.$message({
            //             message: "信息已上传",
            //             type: 'success'
            //         })
            //         sessionStorage.getItem('submitInfo', true)
            //     } else {
            //         //添加失败时 提示后台返回的失败原因  res.data.msg
            //         this.$message({
            //             message: res.data.msg,
            //             type: 'warning'
            //         })
            //     }
            // }).catch(err => {
            //     this.$message({
            //         message: "服务器异常",
            //         type: 'warning'
            //     })
            // })
        },
        submitCaseFile() {
            if (!this.tall || !this.weight) {
                this.$message({
                    message: "请先填写身高体重",
                    type: 'warning'
                })
                return
            }
            //获取地址栏query参数
            let query = this.$route.query
            //记录由哪里跳转到addCase 添加成功之后返回到哪里
            let to = query.to
            if (to === 'manager') {
                //兼容面包屑导航
                this.$store.commit('popBreadcrumbData', 0)
            } else {
                //兼容面包屑导航
                this.$store.commit('popBreadcrumbData', 1)
            }
            //删除地址栏的to参数 以防后面有影响
            delete query.to
            //跳转到之前的页面

            //    let obj1 = {
            //         a:'3',
            //         c:'4'
            //     }
            //    let obj2 = {
            //         a:'1',
            //         b:'2'
            //     }
            //     for(let k in obj2){

            //     }
            //     obj1 = {...obj2}
            this.$router.replace({
                name: to,
                query: {
                    ...query
                }
            })
            execute({
                params: {
                    case_id: this.caseId,
                    flag: 1
                },
                success: (res) => {
                    console.log(res)
                }
            })
            // this.axios.post(`/execute`, {
            //     case_id: this.caseId
            // }).then(res => {
            //     console.log(res);
            // })
            return;
            //下面的不要了 upload组件可以自动上传 不需要再自己掉接口了
            this.axios.post(`/uploads/${this.caseId}`).then(res => {
                if (res.status === 200) {
                    if (res.data.code === 0) {
                        this.$router.replace({
                            name: 'manager'
                        })
                        let data = {
                            name: '患者档案管理',
                            path: '/index/manager'
                        }
                        this.$store.commit('addBreadcrumbData', data)
                    }
                } else {
                    this.$message({
                        message: "服务器异常",
                        type: 'warning'
                    })
                }
            }).catch(err => {
                this.$message({
                    message: "服务器异常",
                    type: 'warning'
                })
            })
        },
        initMessage() {
            this.$message({
                message: '文件已上传完毕，请点击“确认上传”按钮',
                type: 'success'
            })
        }

    },

}
</script>

