<template>
<div class="view2d-page">
    <div class="view2d-page-body">
        <div v-for="(item,index) in imgData" :key="index.toString()" :class="'view2d-page-content'">
            <el-image class="img-view" :src="item" :preview-src-list="getImgDataBig(index)"></el-image>
            <div class="img-name">{{index + 1}}</div>
        </div>
    </div>
</div>
</template>

<script>
import {
    getDDList
} from '../../api'
export default {
    data() {
        return {
            pageSize: 21,
            pageNum: 1,
            imgData: [],
            imgDataBig:[]
        }
    },
    methods: {
        handleCurrentChange(page) {
            this.pageNum = page
        },
        getImgDataBig(index){
            return this.imgDataBig.slice(index).concat(this.imgDataBig.slice(0,index))
        },
        getDDViewData() {
            getDDList({
                params: {
                    case_id: this.$route.query.case_id
                },
                success: res => {
                    let imgUrl = [];
                    let imgDataBig = [];
                    res.data.map(item => {
                        imgUrl.push(`/getFile?file_path=${item}`)
                        let bigImgArr = item.split('/min_')
                        let bigImgStr = bigImgArr.join('/')
                        imgDataBig.push(`/getFile?file_path=${bigImgStr}`)
                    })
                    this.imgData = imgUrl
                    this.imgDataBig = imgDataBig
                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                },
                error: err => {
                    this.$message({
                        message: '服务器异常',
                        type: 'warning'
                    })
                }
            })
            
            // this.axios.get(`/two/${this.$route.query.case_id}`).then(res => {
            //     if (res.status === 200 && res.data.code === 0) {
            //         let imgUrl = [];
            //         res.data.data.map(item => {
            //             imgUrl.push(`/getFile?file_path=${item}`)
            //         })
            //         this.imgData = imgUrl
            //     } else {
            //         this.$message({
            //             message: res.data.msg,
            //             type: 'warning'
            //         })
            //     }
            // }).catch(err => {
            //     this.$message({
            //         message: '服务器异常',
            //         type: 'warning'
            //     })
            // })
        }
    },
    mounted() {
        this.getDDViewData()
    },
    beforeDestroy(){
        // let query = this.$route.query
        // console.log(query)
        // delete query.case_id
        // this.$route.query=query
    }
}
</script>

