<template>
<div class="view3d-page">
    <div ref="container" class="container"> </div>
</div>
</template>

<script>
import * as THREE from 'three'
import OrbitControls from 'three-orbitcontrols'
import {
    OBJLoader
} from 'three-obj-mtl-loader'
import {
    getDDDList
} from '../../api'
export default {
    data() {
        return {
            camera: null,
            scene: null,
            renderer: null,
            container: null,
            mesh: null
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.init()
        })

    },
    beforeDestroy() {
        cancelAnimationFrame(this.canvasId)
    },
    methods: {
        openFullScreen() {
            const loading = this.$loading({
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            return loading;
        },
        closeFullScreen(loading) {
            loading.close();
        },
        init() {
            this.container = this.$refs.container
            //场景
            this.scene = new THREE.Scene();
            this.scene.background = new THREE.Color(0xcce0ff);
            //相机
            const [w, h] = [this.container.offsetWidth * 1, this.container.offsetHeight * 1];
            const camera = new THREE.PerspectiveCamera(
                // 30, w / h, 1000, 10000,
                //把相机离物体最近距离改为10，能离相机更近
                45, w / h, 0.1, 10000,
            );
            camera.position.set(0, 175, 500);
            this.camera = camera;

            //渲染器
            this.renderer = new THREE.WebGLRenderer({
                antialias: true,
            });
            this.renderer.setPixelRatio(window.devicePixelRatio); //设备像素比
            this.renderer.setSize(this.container.offsetWidth * 1, this.container.offsetHeight * 1); //canvas的大小
            this.container.appendChild(this.renderer.domElement); //渲染器在一个canva上绘制输出

            // lights
            var light1 = new THREE.DirectionalLight(0xffffff, 1); //平行光（太阳光）
            light1.position.set(1, 1, 1)
                .normalize();
            this.scene.add(light1);

            var light2 = new THREE.DirectionalLight(0xffffff, 1); //平行光（太阳光）
            light2.position.set(-1, -1, -1)
                .normalize();
            this.scene.add(light2);

            var light3 = new THREE.DirectionalLight(0xffffff, 1); //平行光（太阳光）
            light3.position.set(1, 1, 0)
                .normalize();
            // this.scene.add(light3);

            var light4 = new THREE.DirectionalLight(0xffffff, 1); //平行光（太阳光）
            light3.position.set(0, 1, 1)
                .normalize();
            // this.scene.add(light4);

            var ambientLight = new THREE.AmbientLight(0xffffff); // soft white light
            this.scene.add(ambientLight);

            // var pointLight = new THREE.AreaLight(0xffffff, 1); //面光源
            // AreaLight.position.set(1, 0, 0)
            // this.scene.add(AreaLight);

            var light = new THREE.HemisphereLight(0xffffbb, 0x080820, 1); //半球光
            // this.scene.add(light);
            getDDDList({
                params: {
                    case_id: this.$route.query.case_id
                },
                success: res => {
                    this.openFullScreen()
                    this.mesh = new OBJLoader().load(`/getFile?file_path=${res.data}`, obj => {
                        obj.children[0].geometry.computeBoundingBox();
                        obj.rotation.x = THREE.Math.degToRad(90);
                        (obj.children[0].geometry.boundingBox.max.x + obj.children[0].geometry.boundingBox.min.x) / -2;
                        (obj.children[0].geometry.boundingBox.max.y + obj.children[0].geometry.boundingBox.min.y) / -2;
                        (obj.children[0].geometry.boundingBox.max.z + obj.children[0].geometry.boundingBox.min.z) / -2;
                        obj.children[0].geometry.center()
                        // obj.children[0].color = new THREE.Color(0x8B1817, 1.0)
                        console.log(obj)
                        // 纹理贴图
                        var texture = new THREE.TextureLoader().load(require("../../assets/texture/gan.jpg"));
                        texture.wrapS = THREE.RepeatWrapping;
                        texture.wrapT = THREE.RepeatWrapping;
                        texture.repeat.set(10, 10);
                        obj.children[0].material.map = texture
                        obj.children[0].material.needsUpdate = true
                        obj.children[0].material.side = THREE.DoubleSide
                        this.showLoading = 0
                        this.closeFullScreen(this.openFullScreen())
                        this.scene.add(obj)
                    })

                },
                fail: res => {
                    this.$message({
                        message: res.msg,
                        type: 'warning'
                    })
                }
            })
            //加入对象
            // this.mesh = new OBJLoader().load(`/three/${this.$route.query.case_id}?type=liver`, obj => {

            //     obj.children[0].geometry.computeBoundingBox();

            //     obj.rotation.x = THREE.Math.degToRad(90);
            //     (obj.children[0].geometry.boundingBox.max.x + obj.children[0].geometry.boundingBox.min.x) / -2;
            //     (obj.children[0].geometry.boundingBox.max.y + obj.children[0].geometry.boundingBox.min.y) / -2;
            //     (obj.children[0].geometry.boundingBox.max.z + obj.children[0].geometry.boundingBox.min.z) / -2;
            //     obj.children[0].geometry.center()
            //     // obj.children[0].color = new THREE.Color(0x8B1817, 1.0)
            //     console.log(obj)
            //     // 纹理贴图
            //     var texture = new THREE.TextureLoader().load(require("../../assets/texture/gan.jpg"));
            //     texture.wrapS = THREE.RepeatWrapping;
            //     texture.wrapT = THREE.RepeatWrapping;
            //     texture.repeat.set(10, 10);
            //     obj.children[0].material.map = texture
            //     obj.children[0].material.needsUpdate = true
            //     obj.children[0].material.side = THREE.DoubleSide

            //     this.scene.add(obj)
            // })

            // 这种方式是不会刷新显示的，需要执行一下render
            //renderer.render(scene, camera);

            //添加轨道控制器
            //新建一个轨道控制器
            this.orbitControls = new THREE.OrbitControls(this.camera, this.renderer.domElement);
            this.orbitControls.target = new THREE.Vector3(0, 0, 0); //控制焦点
            this.orbitControls.autoRotate = false; //将自动旋转关闭
            //动态阻尼系数 就是鼠标拖拽旋转灵敏度
            this.orbitControls.dampingFactor = 0.20;
            // //水平旋转范围
            // this.orbitControls.maxAzimuthAngle = 0;
            // this.orbitControls.minAzimuthAngle = 0;
            // //垂直旋转范围
            // this.orbitControls.maxPolarAngle = 150;
            // this.orbitControls.minPolarAngle = 0;
            //平移时相机位置的转换
            // this.orbitControls.screenSpacePaning = true;
            //是否可以缩放
            // this.orbitControls.enableZoom = true;
            //设置相机距离原点的最远距离
            // this.orbitControls.minDistance = 200;
            //设置相机距离原点的最远距离
            // this.orbitControls.maxDistance = 600;
            //是否开启右键拖拽
            // this.orbitControls.enablePan = true;

            this.clock = new THREE.Clock(); //用于更新轨道控制器
            const animate = () => {
                console.log('a')
                this.canvasId = requestAnimationFrame(animate);
                this.camera.updateMatrixWorld();
                let delta = this.clock.getDelta(); //获取时间差
                this.orbitControls.update(delta); //更新时间
                this.renderer.render(this.scene, this.camera);

            };
            animate();

        },

    }
}
</script>

<style scoped>
.view3d-page {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container {
    width: 100%;
    height: 600px;
}

.loading {
    width: 50%;
    height: 50%;
    border: .2em solid transparent;
    border-left-color: currentcolor;
    border-right-color: currentcolor;
    border-radius: 50%;
    -webkit-animation: 1s loading linear infinite;
    animation: 1s loading linear infinite;
}

@-webkit-keyframes loading {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes loading {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
</style>
