                    </div>
                </div>
                <!-- 提交登录按钮 -->
                <div class="login-submit-btn" @click="submitLogin">登 录</div>
                <div class="forget-password">
                    <span @click="forgetPwd">忘记密码？</span>
                </div>
            </div>

            <!-- 注册页面 -->
            <div class="register-form" v-if="activeBtn === 1">
                <span>欢迎注册</span>
                <!-- 注册用户名输入框 -->
                <div class="register-input-view">
                    <i class="iconfont iconicon011"></i>
                    <span class="spliter"></span>
                    <div class="input-view">
                        <input type="text" v-model="registerUsername" placeholder="用户名">
                    </div>
                </div>
                <!-- 注册姓名输入框 -->
