                    <div class="phone-input-view">
                        <span>+86</span>
                    </div>-->
                <!-- 短信验证码  -->
                <!-- <div class="check-view">
                        <div class="message-check-input"></div>
                        <div class="get-checknumber-btn"></div>
                    </div> -->
                <div class="register-submit-btn" @click="submitRegister">
