<script>
let sha1 = require('js-sha1')
import {
    login
} from '../../api'
import {
    department
} from '../../api'
import {
    securityCode
} from '../../api'
import {
    sighIn
} from '../../api'

export default {
    data() {
        return {
            btnGroup: [{
                name: '登录'
            }, {
                name: '注册'
            }],
            activeBtn: 0,
            userName: '',
            pwd: '',
            autoLogin: '',
            rememberPwd: '',

            registerUsername: '',
            registerName: '',
            registerPwd: '',
            registerCheckPwd: '',
            registerDepartment: '',
            optionsDepartment: [],
            areacode: '+86',
            registerPhoneNum: '',
            identifyCode: '',
            // 验证码切换计数
            show: true,
            count: '',
            timer: null,
        }
    },
    mounted() {
        this.getDepartment()
        //判断是否自动登录
        if (localStorage.getItem("autoLogin") === 'true') {
            //如果自动登录的话 从localstrong中取出账号密码直接登录
            this.userName = localStorage.getItem('userName')
            this.pwd = localStorage.getItem('pwd')
            let pwds = sha1(this.pwd)
            login({
                params: {
                    "password": pwds,
                    "username": this.userName
                },
                success: res => {
                    sessionStorage.setItem('doc_id', res.data.doctor_id)
                    sessionStorage.setItem('doc_name', res.data.doctor_name)
                    sessionStorage.setItem('doc_dep', res.data.doctor_dep)
                    this.$router.replace({
                        name: "manager"
                    })
                }
            })

            // this.axios.post('/login', {
            //     "password": localStorage.getItem('pwd'),
            //     "username": localStorage.getItem('userName')
            // }).then(res => {
            //     if (res.status === 200 && res.data.code === 0) {
            //         sessionStorage.setItem('doc_id', res.data.data.doctor_id)
            //         sessionStorage.setItem('doc_name', res.data.data.doctor_name)
            //         sessionStorage.setItem('doc_dep', res.data.data.doctor_dep)
            //         this.$router.replace({
            //             name: "manager"
            //         })
            //     }
            // })

        }
        if (localStorage.getItem("rememberPwd") === 'true') {
            // 记住密码，local storage取数据
            this.userName = localStorage.getItem("userName")
            this.pwd = localStorage.getItem("pwd")
        }
    },
    methods: {
        toggleLogin(index) {
            this.activeBtn = index
        },
        getDepartment() {
            department({
                success: res => {
                    this.optionsDepartment = res.data
                },
                fail: res => {
                    this.$message({
                        message: '服务器异常',
                        type: 'warning',
                    })
                }
            })
            // this.axios.get(`/department`).then(res => {
            //     //成功获取科室信息
            //     // console.log(res)
            //     if (res.status === 200 && res.data.code === 0) {
            //         this.optionsDepartment = res.data.data
            //     } else {
            //         this.$message({
            //             message: '服务器异常',
            //             type: 'warning',
            //         });
            //     }
            // }).catch(err => {
            //     console.log(err)
            // })
        },
        submitLogin() {
            //参数校验：所有不为空
            //保存信息至localstorage
            //判断自动登录和记住密码
            //登陆成功跳转至manager
            if (this.userName && this.pwd) {
                localStorage.setItem("userName", this.userName);
                localStorage.setItem("pwd", this.pwd);
                localStorage.setItem("autoLogin", this.autoLogin);
                localStorage.setItem("rememberPwd", this.rememberPwd);
                let pwds = sha1(this.pwd)
                //向后台发送请求
                login({
                    params: {
                        "password": pwds,
                        // "password": this.pwd,
                        "username": this.userName,
                    },
                    success: (res) => {
                        sessionStorage.setItem('doc_id', res.data.doctor_id)
                        sessionStorage.setItem('doc_name', res.data.doctor_name)
                        sessionStorage.setItem('doc_dep', res.data.doctor_dep)
                        this.$router.replace({
                            name: "manager"
                        })
                    },
                    fail: res => {
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        })
                    },
                    error: err => {
                        this.$message({
                            message: '服务器异常',
                            type: 'warning'
                        })
                    }
                })

                //     this.axios.post('/login', {
                //         "password": pwds,
                //         // "password": this.pwd,
                //         "username": this.userName,

                //     }).then(res => {
                //         //登录成功
                //         if (res.status === 200 && res.data.code === 0) {
                //             sessionStorage.setItem('doc_id', res.data.data.doctor_id)
                //             sessionStorage.setItem('doc_name', res.data.data.doctor_name)
                //             sessionStorage.setItem('doc_dep', res.data.data.doctor_dep)
                //             this.$router.replace({
                //                 name: "manager"
                //             })
                //         } else {
                //             this.$message({
                //                 message: res.data.msg,
                //                 type: 'warning'
                //             })
                //         }
                //     }).catch(err => {
                //         this.$message({
                //             message: '服务器异常',
                //             type: 'warning'
                //         })
                //     })

                // } else {
                //     //test commit
                //     //test commit2
                //     this.$message({
                //         message: '用户信息不能为空',
                //         type: 'warning',
                //         duration: 20000
                //     });
            }

        },
        forgetPwd() {
            this.$router.push({
                name: 'resetpwd'
            })
        },
        getIdentifyCode() {
            if (this.registerPhoneNum) {
                securityCode({
                    params: {
                        "phone_number": this.registerPhoneNum,
                        "pre_phone_number": this.areacode
                    },
                    success: res => {
                        this.$message({
                            message: '正在获取验证码，请稍后',
                            type: 'success'
                        })
                        // this.$router
                    },
                    fail: res => {
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        })
                    },
                    error: err => {
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        })
                    }
                })

                // this.axios.post(`/identify`, {
                //     "phone_number": this.registerPhoneNum,
                //     "pre_phone_number": this.areacode
                // }).then(res => {
                //     if (res.status === 200 && res.data.code === 0) {
                //         this.$message({
                //             message: '正在获取验证码，请稍后',
                //             type: 'success'
                //         })
                //         this.$router
                //     } else {
                //         this.$message({
                //             message: res.data.msg,
                //             type: 'warning'
                //         })
                //     }
                // }).catch(err => {
                //     this.$message({
                //         message: res.data.msg,
                //         type: 'warning'
                //     })
                // })
            } else {
                this.$message({
                    message: '请输入手机号码',
                    type: 'warning'
                })
            }
        },
        getCode() {
            const TIME_COUNT = 60;
            this.getIdentifyCode()
            if (!this.timer) {
                this.count = TIME_COUNT;
                this.show = false;
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                        this.count--;
                    } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000)
            }
        },
        submitRegister() {
            // 参数校验：所有不为空；两个密码相等；
            if (this.registerUsername && this.registerUsername && this.registerPwd && this.registerCheckPwd && this.registerDepartment) {
                // 上传信息至后台
                // 注册成功跳转至login
                let registerPwdS = sha1(this.registerPwd)
                sighIn({
                    params: {
                        "department": this.registerDepartment,
                        "identify": this.identifyCode,
                        "name": this.registerName,
                        // "password":this.registerPwd,
                        "password": registerPwdS,
                        "phone_number": this.registerPhoneNum,
                        "pre_phone_number": "+86",
                        "username": this.registerUsername
                    },
                    success: res => {
                        this.$message({
                            message: '注册成功，请登录',
                            type: 'success',
                        })
                        this.activeBtn = 0;
                    },
                    fail: res => {
                        this.$message({
                            message: res.msg,
                            type: 'error',
                        })
                    },
                    error: err => {
                        this.$message({
                            message: res.msg,
                            type: 'error',
                        })
                    }
                })
                // this.axios.post('/sign_in', {
                //     "department": this.registerDepartment,
                //     "identify": this.identifyCode,
                //     "name": this.registerName,
                //     // "password":this.registerPwd,
                //     "password": registerPwdS,

                //     "phone_number": this.registerPhoneNum,
                //     "pre_phone_number": "+86",
                //     "username": this.registerUsername
                // }).then(res => {
                //     if (res.status === 200 && res.data.code === 0) {
                //         this.$message({
                //             message: res.data.msg,
                //             type: 'success',
                //         })
                //         this.activeBtn = 0;
                //     } else {
                //         this.$message({
                //             message: res.data.msg,
                //             type: 'error',
                //         })
                //     }
                // }).catch(err => {
                //     this.$message({
                //         message: res.data.msg,
                //         type: 'error',
                //     })
                // })

            } else if (this.registerPwd !== this.registerCheckPwd) {
                this.$message({
                    message: '密码不一致',
                    type: 'error',
                })
            } else {
                this.$message({
                    message: '表单内容不能为空',
                    type: 'error',
                })
            }
        }
    }
}
</script>

